@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@500&display=swap);
html, body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: all linear 500ms;
  background-image: var(--body-background);
  background-repeat: no-repeat;
  outline: none;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  --font-size:3rem;
  --header-size:5rem;
  --body-background:linear-gradient(to bottom, #081221,#03080f );
  --color-card-background:#0e1a2b;
  --color_panel_background:#0e1a2b;
  --color_button_text:#ffffff;
  --color_headings:#ffffff;
  --color_labels:#ffffff;
  --color_text:#c1d1e8;
  --color_input_background:#182c47;
  --color_button:#5692e8
}
.App {
  font-family: Mulish, serif;
  height: 100%;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.heading, .label, button{
  font-family: Montserrat, sans-serif;

}

.max-len-3{
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

p{
  margin: 0;
  padding: 0;
}

.page{
    min-height: 100vh;
    padding: 4rem;
    background-position: center;

}

.nav-container{
    display: flex;
    position: relative;
}

.nav-bg-indicators{
    display: flex;
    flex-direction: row;

}

h1, h2, h3, h4, h5, h6{
    color: var(--color_headings);
    margin: 0;
    padding: 0;
}

.nav-item-container{
    position: relative;
    width: 20rem;
}

.nav-item-container h1{
    padding: 0;
    margin: 0;
    font-size: 3.5rem;

}

.nav-item{
    position: absolute;
    text-decoration: none;
    color: var(--color_headings);
    font-size: 1.5rem;
    bottom: 0.05rem;
    left: 2rem;
    right: 0;
    font-weight: 500;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
}

.nav-item:hover{
    text-decoration: none;
    color: white;
    font-weight: 700;

}







.text-input{
    background: var(--color_input_background);
    height: 2rem;
    border: none;
    margin-top: 0.6rem;
    outline: none;
    font-size: 16px;
    padding: 0 0.5rem;
    color:  var(--color_text);
}

button{
    background: var(--color_button);
    border: none;
    color: white;
    padding: 0.5rem 1.4rem;
}

.text{
    color: var(--color_text);
}

@media screen and  (max-width:820px)
{
    .page{
        padding: 2rem;
        height: 100%;
        width: 100%;
    }
    .nav-item-container{
        position: relative;
        width: 15rem;
    }

    .nav-item-container h1{
        padding: 0;
        margin: 0;
        font-size: 2.5rem;

    }
    .nav-item{
        position: absolute;
        text-decoration: none;
        color: var(--color_headings);
        font-size: 1.5rem;
        bottom: 0.05rem;
        left: 2rem;
        right: 0;
        font-weight: 500;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        min-width: -webkit-fit-content;
        min-width: -moz-fit-content;
        min-width: fit-content;
    }
}

@media  screen and  (max-width:660px)
{
    .nav-container{
        display: flex;
        flex-direction: column;
    }
    .nav-item-container{
        position: relative;
        width: 100%;
    }

    .nav-bg-indicators{
        display: flex;
        flex-direction: column;
    }
}

.home-page-container{
    display: grid;
    grid-template-columns: 20% 73%;
    grid-column-gap: 2%;
}


.filter-wrapper{
    width: 100%;
    padding: 1.2rem;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;

}

.card{
    background: var(--color-card-background);
}


.filter-param-wrapper{
    margin-top: 4rem;
}

.input-wrapper{
    margin-bottom: 24px;
}

.input-wrapper{
    display: flex;
    flex-direction: column;
}

.input-wrapper label{
    color: white;
}

.button-clear{
    float: right;
    outline: none;
    cursor: pointer;
    margin-top: 16px;
}
.button-clear:active{
    opacity: 0.8;
}

.videos-wrapper{
    display: flex;
    flex-direction: column;
    margin-left: 3rem;
    height: 70vh;
    overflow-y: scroll;
}

.tab-control-container{

}


@media screen and (max-width:1024px)
{
   .home-page-container{
       flex-direction: column;
       display: grid;
       grid-template-columns: 100%;
       height: 100%;
   }

    .videos-wrapper{
        margin-left: 0;
        margin-top: 2rem;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;

    }

    .filter-wrapper{
        padding: 1rem;
        width: 100%;

    }

    .tab-control-container{
        display: grid;
        grid-template-columns: 20% 60% 10%;
        grid-column-gap: 4%;
    }

    .tab-control-container > .input-wrapper{
        margin-bottom: 1rem;
    }

    .filter-item-container h3{
        padding: 0;
        margin: 0;
    }

    .filter-item-container > .input-wrapper:nth-child(1){
       margin-top: -2rem;
    }

    .clear-button-container{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;
        margin-bottom: 1.6rem;
    }
    .button-clear{
        float: bottom;
        height: 2rem;
        padding: 0 1rem;
    }

    .filter-param-wrapper{
        margin-top: 2rem;
    }

}


@media screen and  (max-width:660px)
{

    .home-page-container{
        display: grid;
        grid-template-columns: 100%;
        height: 100%;

    }

    .tab-control-container{
        display: grid;
        grid-template-columns: 100%;
        grid-column-gap: 4%;
        margin-top: 0;
    }

    .button-clear{
        width: 100%;
    }

    .input-wrapper{
        margin-bottom: 1rem;
    }
    .filter-wrapper{
        padding: 1rem;
        width: 100%;

    }
    .videos-wrapper{

    }
}
.video-item-wrapper{
    min-height: 10rem;
    display: flex;
    margin-bottom: 2rem;
    flex-direction: row;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.video-container{
    min-width: 10rem;
    background: black;
    height: 10rem;
    position: relative;
}


.video-details-wrapper{
    display: flex;
    width: 100%;
}

.video-details-wrapper p{
    max-lines: 3;
}

.video-details{
    padding: 1rem;
    display: flex;
    flex-direction: column;
    flex: 1 1;

}

.video-number-container{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 6rem;
}

.video-number{
    font-size: 1.6rem;
    height: 3rem;
    width: 3rem;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content:center ;
    color: white;

    background: var(--color_button);
}
.video-number-mobile{
    display: none;
}


@media screen and  (max-width:600px)
{
    .video-item-wrapper{
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        display: flex;
        margin-bottom: 2rem;
        flex-direction: column;
        position: relative;
    }

    .video-item-wrapper card{
        display: flex;
        flex-direction: column;
    }

    .video-container{
        height: 10rem;
    }

    .video-number-container{
        position: absolute;
        top: 1rem;
        right: 1rem;
    }

    .video-number-mobile{
        position: absolute;
        top: 1rem;
        right: 1rem;
        display: flex;
        font-size: 1.5rem;
        height: 3rem;
        width: 3rem;
        border-radius: 100%;
        align-items: center;
        justify-content:center ;
        color: white;
        background: var(--color_button);

    }

    .video-number-container{
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }

    .video-number{
        display: none;
    }
}


.title {
    text-transform: uppercase;
}

.root {
    display: flex;
    justify-content: center;
    height: 100%;
}

.wrapper {
    width: 40%;
}


.information {
    color: var(--color_text);
    margin-top: 16px;
}

.name-email-container {
    display: flex;
    margin-top: 16px;
}

.message-container {
    margin-top: 16px;
}

.label {
    color: var(--color_text);
    font-size: 90%;
}

.display-block {
    display: block;
    width: 100%;
}

.form-title {
    color: var(--color_text);
}

.form {
    background-color: var(--color-card-background);
    padding: 20px;
    margin-top: 16px;
}

.name-container {
    flex-grow: 1;
    margin-right: 8px;
}

.email-container {
    flex-grow: 1;
    margin-left: 8px;
}

* {
    box-sizing: border-box;
}

.message-text-area {
    background-color: var(--color_input_background);
    border: none;
    margin-top: 16px;
    resize: none;
}

.form-title {

}

.send-button {
    background-color: var(--color_button);
    color: var(--color_button_text);
    border: none;
    padding: 6px 16px;
}

.button-container {
    text-align: end;
    margin-top: 16px;
}

.required {
    color: var(--color_button);
}

/*small desktop*/
@media screen and (max-width: 1200px) {
    .wrapper {
        width: 50%;
    }

}

/*tablet*/
@media screen and (max-width: 1024px) {
    .wrapper {
        width: 70%;
    }

    .name-email-container {
        display: flex;
        margin-top: 16px;
        flex-direction: column;
    }

    .name-container {
        flex-grow: 1;
    }

    .email-container {
        flex-grow: 1;
        margin-left: 0;
        margin-top: 16px;
    }
}

/*mobile*/
@media screen and (max-width: 660px) {
    .wrapper {
        width: 100%;
    }

    .name-email-container {
        display: flex;
        margin-top: 16px;
        flex-direction: column;
    }

    .name-container {
        flex-grow: 1;
    }

    .email-container {
        flex-grow: 1;
        margin-left: 0;
    }
}
